import React from "react";
import p5 from "p5";

import "./algorithm_selection.css";

import GraphAlgorithm from "../algorithms/graph/main"

class Graph extends React.Component {
    constructor(props) {
        super(props);
        this.graph_sketch = React.createRef();
    }

    componentDidMount() {
        this.p5 = new p5(GraphAlgorithm, this.graph_sketch.current);
        this.p5.algorithm = null;
    }

    changeAlgorithm(algorithm) {
        this.p5.algorithm = algorithm;
    }

    render() {
        return (
            <div>
                <div className="sketch" ref={this.graph_sketch}>
                </div>
                <header className="alogrithms">
                    <button onClick={() => this.changeAlgorithm("prims")}>Prim's</button>
                    <div className="alogrithms-right">
                        <button onClick={() => {this.p5.randomNodes = true}}>Randomize</button>
                        <button onClick={() => {this.p5.reset = true}}>Reset</button>
                    </div>
                </header>
            </div>
        )
    }
}

export default Graph