import React from "react";
//import useScript from "../hooks/useScript";
import p5 from "p5";
import clock from "../algorithms/clock"

import "./Home.css";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.home_sketch = React.createRef();
    }

    componentDidMount() {
        this.p5 = new p5(clock, this.home_sketch.current);
    }

    render() {
        return (
            <div className="sketch" ref={this.home_sketch}>
            </div>
        )
    }
}

export default Home;