

function Prims(nodes) {

    var reached = []
    var unreached = []
    
    for (let i = 0; i < nodes.length; i++) {
        unreached.push(nodes[i]);
    }
  
    reached.push(unreached[0]);
    unreached.splice(0, 1);
      
    while (unreached.length > 0) {
        let record = 10000;
        var rIndex = 0;
        var uIndex = 0;
        
        for (let i = 0; i < reached.length; i++) {
            for (let j = 0; j < unreached.length; j++) {
                let v1 = reached[i];
                let v2 = unreached[j];
                
                let d = Math.sqrt( Math.pow(v1["x"]-v2["x"], 2) + Math.pow(v1["y"] - v2["y"], 2) )
                if (d < record) {
                    record = d;
                    rIndex = i;
                    uIndex = j;
                }
            
            }
        }
        
        reached[rIndex]["connections"].push(unreached[uIndex]);
      
        reached.push(unreached[uIndex]);
        unreached.splice(uIndex, 1);
    }

    return reached;
}



export default Prims;