import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";

import './App.css';

import { Navbar, Home, Graph } from "./components";

function App() {
	return (
		<div className="App">
			<Router>
				<Navbar />
				<Switch>
					<Route path="/graph_algorithms" component={() => <Graph />} />
					<Route exact path="/" component={() => <Home />} />
				</Switch>
			</Router>
		</div>
	);
}

export default App;
