import { Prims } from "../graph"

const GraphAlgorithm = p => {

	var nodes = [];
	let tool = "node";
	let radius = 20;
	const minY = p.windowHeight * 0.12;
	const maxY = p.windowHeight * 0.89;
  
    p.setup = () => {
		p.createCanvas(p.windowWidth, p.windowHeight);


    }
  
    p.draw = () => {
		p.background(0);

		if (p.reset) {
			nodes = [];
			p.reset = false;
		}

		if (p.randomNodes) {
			for (let i = 0; i<10; i++) {
				nodes.push({
					"x": Math.floor(Math.random() * p.windowWidth),
					"y": Math.floor(Math.random() * (maxY-minY) + minY),
					"connections": []
				})
			}
			p.randomNodes = false;
		}

		if (nodes.length > 0) {

			nodes.forEach(node => {
				node["connections"] = [];
			})
			
			execute();

			nodes.forEach(node => {
				p.circle(node["x"], node["y"], radius);
				p.stroke(255);
				node["connections"].forEach(connection => {
					p.line(node["x"], node["y"], connection["x"], connection["y"]);
				})
			});
		}
	}

	p.mouseClicked = () => {
		if (tool === "node") {

			if (maxY < p.mouseY || minY > p.mouseY) { return }

			nodes.push({
				"x": p.mouseX,
				"y": p.mouseY,
				"connections": []
			})
		}
	}

	const execute = () => {

		if (p.algorithm === "prims") {
		
			nodes = Prims(nodes);
		}

	}
}

export default GraphAlgorithm;